import React from "react";
import foto2 from './img/foto2.jpg';
import foto3 from './img/foto3.jpg';
import foto4 from './img/foto4.jpg';
import foto5 from './img/foto5.jpg';

export default function Carrossel(){
    return(
        <article class="d-flex align-items-center justify-content-center m-auto" data-aos="fade-up">
            <div id="carousel" class="carousel slide carousel-fade" data-bs-ride="carousel">
                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img class="d-block w-100" src={foto2} alt="Foto de perfil"></img>
                    </div>
                    <div class="carousel-item">
                        <img class="d-block w-100" src={foto3} alt="Foto de perfil"></img>
                    </div>
                    <div class="carousel-item">
                        <img class="d-block w-100" src={foto4} alt="Foto de perfil"></img>
                    </div>
                    <div class="carousel-item">
                        <img class="d-block w-100" src={foto5} alt="Foto de perfil"></img>
                    </div>
                </div>
            </div>
        </article>
    )
}


