import React from 'react';
import Music from './audio/music.mp3'

export default function Header(){
    const styleaudio={
        width: '250px',
        height: '30px',
        margin: '4px'
    };
    return(
        <header class="d-flex justify-content-center">
            <audio controls style={styleaudio}>
                <source src={Music} type="audio/mpeg"></source>
            </audio>
        </header>
    )
}


